import {Component} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {SACRAMENT_PROGRAMS} from '../../shared/data';

@Component({
  selector: 'app-sacrament-program-page',
  template: `
    <app-navigation></app-navigation>
    <div class="l41-container container pt-4 mrg-horizon-auto">
      <app-sacrament-program [program]="program"></app-sacrament-program>
    </div>
    <app-footer></app-footer>
  `
})
export class SacramentProgramPageComponent {
  program = SACRAMENT_PROGRAMS.filter(p => p.id === Math.floor(this.actRoute.snapshot.params.id))[0];

  constructor(
    private actRoute: ActivatedRoute
  ) {}
}
