import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {PageNotFoundPageComponent} from './pages/page-not-found-page/page-not-found-page.component';
import {SacramentProgramPageComponent} from './pages/sacrament-program-page/sacrament-program-page.component';
import {SafetyTipsPageComponent} from './pages/safety-tips-page/safety-tips-page.component';
import {CheatSheetPageComponent} from './pages/cheat-sheet-page/cheat-sheet-page.component';
import {LoginComponent} from './pages/login/login.component';
import {ZoomSacramentMeetingPageComponent} from './pages/zoom-sacrament-meeting-page/zoom-sacrament-meeting-page.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'home',
    component: ZoomSacramentMeetingPageComponent
  },
  // {
  //   path: 'home',
  //   component: HomePageComponent
  // },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'cheat-sheet',
    component: CheatSheetPageComponent
  },
  {
    path: 'safety-tips',
    component: SafetyTipsPageComponent
  },
  {
    path: 'sacrament-program/:id',
    component: SacramentProgramPageComponent
  },
  {
    path: '**',
    component: PageNotFoundPageComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
